@use "global" as *;

.campaign-card {
  margin-top: rem(27);
  flex: 0 0 auto;
  width: 100%;
  max-width: rem(280);
  height: auto;
  @include mq("md") {
    margin-top: 0;
    max-width: rem(333);
  }
}

.campaign-card:hover .campaign-card__img {
  opacity: 0.7;
}

.campaign-card a {
  display: flex;
  height: 100%;
  box-shadow: rem(2) rem(2) rem(10) 0 rgba(0, 0, 0, 0.25);
}

.campaign-card__item {
  display: flex;
  flex-direction: column;
}

.campaign-card__img {
  width: 100%;
  transition: 0.3s;
}

.campaign-card__img img {
  width: 100%;
  aspect-ratio: 280 / 188;
  object-fit: cover;
}

.campaign-card__content {
  padding: rem(16) rem(16) rem(20.5) rem(16);
  display: flex;
  flex-direction: column;
  height: 100%;
  @include mq("md") {
    padding: rem(23) rem(17) rem(36.422) rem(16);
  }
}

.campaign-card__category {
  padding: rem(5) rem(10) rem(1) rem(11);
  font-size: rem(12);
  color: $green;
  line-height: calc(22 / 12);
  border: rem(1) solid $green;
  min-width: rem(108);
  width: fit-content;
  @include mq("md") {
    padding: rem(7) rem(15) rem(4) rem(15);
  }
}

.campaign-card__title {
  position: relative;
  margin-top: rem(7);
  font-size: rem(16);
  line-height: calc(24 / 16);
  font-weight: $medium;
  color: $black1;
  @include mq("md") {
    margin-top: rem(6);
  }
}
