.campaign-card__description {
  margin-top: rem(15);
  position: relative;
  @include mq("md") {
    padding: rem(22) rem(15) 0 rem(19);
    margin-top: auto;
  }
}

.campaign-card__description::before {
  display: inline-block;
  content: "";
  position: absolute;
  top: rem(-6);
  left: 0;
  width: 100%;
  height: rem(1);
  background-color: $green;
  border-radius: rem(1);
  @include mq("md") {
    top: rem(12);
  }
}

.campaign-card__description-text {
  font-size: rem(14);
  line-height: calc(24 / 14);
  color: $black1;
  text-align: center;
  @include mq("md") {
    font-size: rem(16);
    line-height: calc(24 / 16);
    font-family: "Noto Sans JP", serif;
  }
}

.campaign-card__price {
  margin-top: rem(6);
  text-align: center;
  @include mq("md") {
    margin-top: rem(7);
  }
}

.campaign-card__price-original {
  position: relative;
  font-size: rem(20);
  line-height: 1;
  color: $black1;
  font-weight: $bold;
  font-family: "Lato", sans-serif;
  padding: 0 rem(1.5) 0 0;
  @include mq("md") {
    font-size: rem(24);
    padding: 0 rem(1.5) 0 0;
  }
}
.campaign-card__price-original::before {
  display: inline-block;
  content: "";
  position: absolute;
  bottom: rem(11);
  left: 0;
  width: 100%;
  height: rem(3);
  background-color: $red;
  @include mq("md") {
    bottom: rem(12.8);
  }
}

.campaign-card__price-discounted {
  margin-left: rem(12);
  font-size: rem(32);
  line-height: 1;
  color: $green;
  font-weight: $bold;
  font-family: "Lato", sans-serif;
  @include mq("md") {
    font-size: rem(40);
    margin-left: rem(21);
  }
}
